:root {
  --font-stack-serif:Constantia, Lucida Bright, Lucidabright, Georgia, serif;
  --font-stack-sansserif:Open Sans, Helvetica Neue, Helvetica, Arial, sans-serif;
  --font-stack-general:Open Sans, Helvetica Neue, Helvetica, Arial, sans-serif;
  --font-stack-heading:Open Sans, Helvetica Neue, Helvetica, Arial, sans-serif;
  --font-stack-monospace:Consolas, Ubuntu Mono, Lucida Console, Courier New, Courier, monospace;
  --font-size-base:16px;
  --font-size-general:1rem;
  --font-size-big:1.1em;
  --font-size-xbig:1.2em;
  --font-size-xxbig:1.3em;
  --font-size-small:0.9em;
  --font-size-xsmall:0.8em;
  --font-size-xxsmall:0.7em;
  --font-size-h1:2em;
  --font-size-h2:1.6em;
  --font-size-h3:1.4em;
  --font-size-h4:1.2em;
  --font-weight-normal:400;
  --font-weight-bold:700;
  --font-weight-heading:700;
  --line-height-general:1.5;
  --line-height-h1:1.15;
  --line-height-h2:1.25;
  --line-height-h3:1.35;
  --line-height-h4:1.45;
  --line-height-general-in-rem:1.5rem;
  --line-height-h1-in-rem:1.15rem;
  --line-height-h2-in-rem:1.25rem;
  --line-height-h3-in-rem:1.35rem;
  --line-height-h4-in-rem:1.45rem;
  --color-primary:#dc2f3c;
  --color-dark-accent:#587b98;
  --color-light-accent:#76b1df;
  --color-background:#ffffff;
  --color-contrast:#252428;
  --color-contrast-medium:#8c8c8c;
  --color-contrast-low:#bfbfbf;
  --color-contrast-lower:#d9d9d9;
  --color-contrast-lowest:#f2f2f2;
  --color-contrast-high:#737373;
  --color-contrast-higher:#595959;
  --color-contrast-highest:#404040;
  --color-success:#27ae60;
  --color-alert:#c0392b;
  --color-warning:#d35400;
  --color-highlight:#f1c40f;
  --color-dribbble:#ea4c89;
  --color-facebook:#3b5998;
  --color-flickr:#ff0084;
  --color-instagram:#517fa4;
  --color-linkedin:#0077b5;
  --color-medium:#00ab6b;
  --color-pinterest:#bd081c;
  --color-rss:#f26522;
  --color-tumblr:#35465c;
  --color-twitter:#55acee;
  --color-youtube:#b31217;
  --space:1.5rem;
  --space-top-h:calc(var(--space) * 1.25);
  --space-bottom-h:calc(var(--space) * 0.875);
  --alley:30px;
  --p-inline-t:0.125rem;
  --p-inline-r:0.25rem;
  --p-inline-b:0.125rem;
  --p-inline-l:0.25rem;
  --padding-inline:var(--p-inline-t) var(--p-inline-r) var(--p-inline-b) var(--p-inline-l);
  --p-box-t:0.75rem;
  --p-box-r:1.25rem;
  --p-box-b:0.75rem;
  --p-box-l:1rem;
  --padding-box:var(--p-box-t) var(--p-box-r) var(--p-box-b) var(--p-box-l);
  --p-message-t:0.75rem;
  --p-message-r:3.25rem;
  --p-message-b:0.75rem;
  --p-message-l:1rem;
  --padding-message:var(--p-message-t) var(--p-message-r) var(--p-message-b) var(--p-message-l);
  --p-table-t:0.5rem;
  --p-table-r:2rem;
  --p-table-b:0.5rem;
  --p-table-l:0;
  --padding-table:var(--p-table-t) var(--p-table-r) var(--p-table-b) var(--p-table-l);
  --p-button-t:0.425em;
  --p-button-r:1em;
  --p-button-b:0.425em;
  --p-button-l:1em;
  --padding-button:var(--p-button-t) var(--p-button-r) var(--p-button-b) var(--p-button-l);
  --p-input-t:0.5rem;
  --p-input-r:0.75rem;
  --p-input-b:0.5rem;
  --p-input-l:0.75rem;
  --padding-input:var(--p-input-t) var(--p-input-r) var(--p-input-b) var(--p-input-l);
  --breakpoint-s:480px;
  --breakpoint-m:768px;
  --breakpoint-l:960px;
  --breakpoint-xl:1200px;
  --ease-in-out-expo:cubic-bezier(1, 0, 0, 1);
  --ease-in-out-quart:cubic-bezier(0.77, 0, 0.175, 1);
  --default-easing:cubic-bezier(1, 0, 0, 1);
  --border-size:1px;
  --max-site-width:1200px;
}

html {
  overflow-y: scroll;
  box-sizing: border-box;
  font-size: var(--font-size-base);
  -ms-text-size-adjust: 100%;
}

html > div,
html > span {
  display: none;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--color-background);
  color: var(--color-contrast);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.05);
  word-wrap: break-word;
  font-weight: var(--font-weight-normal);
  font-family: var(--font-stack-general);
  line-height: var(--line-height-general);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-wrap: break-word;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  font-weight: inherit;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}

main,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul,
pre,
address,
blockquote,
dl,
div,
