.table-container {
  overflow-x: auto;
}

.video-container {
  padding-bottom: 56.25%;
  position: relative;
  display: block;
  height: 0;
}

.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.screen-reader-text {
  position: absolute;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  word-wrap: normal;
  clip-path: inset(50%);
}

